import { useTranslation } from "react-i18next";
import style from "../../FinanceManager.module.css";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import { useCallback, useState } from "react";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FinanceOtherMiscellaneousNewReceipt from "./FinanceOtherMiscellaneousNewReceipt";
const FinanceOtherMiscellaneous = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);
  const onNavigate = useCallback(() => {
    let url = "";
    url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/finance`
        : `/q/${params.username}/member/staff/finance`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "0.2rem",
              }}
            >
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onNavigate}
              />
              <h6>{t("miscellaneous_fees_collect")}</h6>
            </div>
            <div
              className={style.fm_header_start_container}
              style={{
                gap: "1rem",
              }}
            >
              <CreateButton
                label="new_receipt"
                //   onAction={onNavigateReceipt}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
              <CreateButton
                label="print"
                //   onAction={onNavigateReceipt}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
              <CreateButton
                label="generate"
                //   onAction={onNavigateReceipt}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
              <CreateButton
                label="delete_receipt"
                //   onAction={onNavigateReceipt}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 1rem",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={
                getQuery.state?.urlOffSet === "Institute"
                  ? `/q/${params.username}/institute/member/finance?type=miscellaneous-receipt${tab.urlKey}`
                  : `/q/${params.username}/member/staff/finance?type=miscellaneous-receipt${tab.urlKey}`
              }
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginTop: "0",
          }}
        />
        <UserMemberContentWrapper>
          {searchParams.get("n") === "history" ? null : (
            <FinanceOtherMiscellaneousNewReceipt />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default FinanceOtherMiscellaneous;
const tabList = [
  {
    label: "receipt",
    id: "receipt",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "transaction",
    id: "history",
    urlKey: "&n=history",
    groupIs: ["history"],
  },
];
